  padding: 8px;
  font-size: 18px;
}

.search-form button {
  padding: 8px 12px;
  font-size: 18px;
}

.results-container {
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px;
  display: flex;
}

.results-container img {
  max-width: 400px;
  margin: 12px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
